.video {
  width: 100%;
  /* .video__link */
  /* .video__desc */
}
.video__wrap {
  position: relative;
  display: block;
  border-radius: 6px;
  overflow: hidden;
  padding-bottom: 56.6%;
}
.video__wrap:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  opacity: 0.5;
  z-index: 1;
}
.video__wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  left: 0;
  top: 0;
}
.video__wrap:hover:before {
  opacity: 1;
}
.video__wrap:hover .video__link:after {
  animation: video 0.5s infinite alternate;
}
.video__link {
  position: absolute;
  color: #FD9F32;
  font-size: 54px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  line-height: 0;
  border-radius: 50%;
}
.video__link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid #FFEDD7;
  left: 0;
  top: 0;
  transition: all 0.3s;
}
.video__link:before {
  transform: scale(1.5);
}
.video__link:after {
  opacity: 0;
}
.video__desc {
  margin-top: 1em;
  font-size: 1.125rem;
  font-weight: bold;
  text-decoration: none;
}

.marker {
